<template>
  <div>
    <div v-if="tipoDeIngreso == 'Asalariado'">
      <span class="checkbox-apoyo span">Quieres utilizar:</span>
      <v-checkbox
        @click="seleccionarApoyo('cofinavit')"
        label="Cofinavit"
        class="checkbox-apoyo"
        v-model="cofinavit"
        color="#13d8f2"
      ></v-checkbox>
      <v-checkbox
        @click="seleccionarApoyo('apoyoInfonavit')"
        label="Apoyo Infonavit"
        class="checkbox-apoyo"
        v-model="apoyoInfonavit"
        color="#13d8f2"
      ></v-checkbox>
      <v-checkbox
        @click="seleccionarApoyo('ninguno')"
        label="Ning√∫no"
        class="checkbox-apoyo"
        v-model="ninguno"
        color="#13d8f2"
      ></v-checkbox>
    </div>

    <div>
      <v-text-field
        v-show="cofinavit && tipoDeIngreso == 'Asalariado'"
        class="no-margin"
        v-model="montoDeApoyo"
        label="Con cuanto cuentas en cofinavit"
        color="#13D8F2"
      ></v-text-field>
    </div>
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex";

export default {
  data() {
    return {
      cofinavit: false,
      apoyoInfonavit: false,
      ninguno: false,

      montoDeApoyo: ""
    };
  },
  computed: {
    montoDeApoyoComputado() {
      return this.montoDeApoyo;
    },
    ...mapState(["tipoDeIngreso"])
  },
  methods: {
    seleccionarApoyo(apoyo) {
      switch (apoyo) {
        case "cofinavit":
          this.cofinavit = true;
          this.apoyoInfonavit = false;
          this.ninguno = false;
          this.actualizarTipoApoyo(apoyo)
          this.actualizarApoyo(this.montoDeApoyoComputado);
          break;

        case "apoyoInfonavit":
          this.cofinavit = false;
          this.apoyoInfonavit = true;
          this.ninguno = false;
          this.actualizarTipoApoyo("apoyoInfonavit")
          this.actualizarApoyo("");
          break;

        case "ninguno":
          this.cofinavit = false;
          this.apoyoInfonavit = false;
          this.ninguno = true;
          this.actualizarTipoApoyo("ninguno")
          this.actualizarApoyo("");
          break;
      }

      setTimeout(()=>{
        this.$forceUpdate();
      },1000)
    },
    ...mapMutations(["actualizarApoyo","actualizarTipoApoyo"])
  },
  watch: {
    montoDeApoyo() {
      this.actualizarApoyo(this.montoDeApoyo);
    }
  }
};
</script>
