.v-input.checkbox-apoyo.v-input--selection-controls.v-input--checkbox.theme--light {
  display: inline-block;
  margin: 0px;
  margin-right: 20px;
}

.v-input.checkbox-apoyo.v-input--selection-controls.v-input--checkbox.theme--light
  * {
  margin: 0;
}

.v-input.checkbox-apoyo.v-input--selection-controls.v-input--checkbox.theme--light
  .v-messages.theme--light {
  display: none;
}

.checkbox-apoyo.span {
  position: relative;
  display: inline-block;
  margin-right: 20px;
  bottom: 8px;
  font-size: 16px;
    color :rgba(0,0,0,0.54)
}

@media only screen and (max-width: 600px) {
  .checkbox-apoyo.span {
    display: block;
    text-align: left;
    font-size: 16px;
    color :rgba(0,0,0,0.54)
  }
  .checkbox-apoyo .v-input__control .v-input__slot label.v-label{
    font-size: 10px !important
  }
}
</style>
