<template>
  <div>
    <v-text-field
      class="no-margin"
      v-model="ingresoMensual"
      label="Tu Ingreso Mensual"
      color="#13D8F2"
    ></v-text-field>
  </div>
</template>
<script>
import { mapMutations } from "vuex";

export default {
  data() {
    return {
      ingresoMensual: ""
    };
  },
  methods: {
    ...mapMutations(["actualizarIngresoMensual"])
  },
  watch: {
    ingresoMensual() {
      this.actualizarIngresoMensual(this.ingresoMensual);
    }
  }
};
</script>

