<template>
  <div>
    <div class="navbarFixed" v-if="navbarFixed">
      <div class="width-3 center navbar">
        <TipoDePago type="fixed"/>
        <Plazo/>
      </div>
    </div>
    <div>
      <TipoDePago type="static"/>
      <Plazo/>
    </div>
  </div>
</template>
<script>
import TipoDePago from "./opcionesEstaticas/TipoDePago.vue";
import Plazo from "./opcionesEstaticas/Plazo.vue";

export default {
  components: {
    TipoDePago,
    Plazo
  },
  data(){return{
      scroll: 0,
  }},
  computed: {
    navbarFixed() {
      if (this.scroll >= 290) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    scrollUpdate() {
      this.scroll = window.scrollY;
    }
  },
  mounted() {
    window.addEventListener("scroll", this.scrollUpdate, false);
  }
};
</script>
